.wp-block-zolo-newsletter {
    .zolo-newsletter-form {
        --newsletter-wrap-padding: 40px;
        --newsletter-wrap-radius: 0.5rem;
        --newsletter-title-margin: 0 0 30px;
        --newsletter-title-color: #0e0e0f;
        --newsletter-form-label-gap: 10px;
        --newsletter-form-label-color: #48484b;
        --newsletter-input-padding: 14px 15px;
        --newsletter-input-border-width: 1px;
        --newsletter-input-border-color: #e7e7e7;
        --newsletter-input-border-style: solid;
        --newsletter-input-radius: 6px;
        --newsletter-input-color: #5e5e60;
        --newsletter-input-focus-color: #1c64f2;
        --newsletter-btn-padding: 16px 20px;
        --newsletter-btn-radius: 6px;
        --newsletter-btn-color: #fff;
        --newsletter-form-item-gap: 25px;
        --newsletter-btn-icon-gap: 8px;

        display: flex;
        flex-direction: column;
        gap: var(--newsletter-form-item-gap);
        position: relative;
    }

    .zolo-newsletter-title {
        margin: var(--newsletter-title-margin);
        font-size: 24px;
        color: var(--newsletter-title-color);
        text-transform: capitalize;
    }

    .zolo-form-control {
        display: flex;
        justify-content: center;
        flex-direction: column-reverse;
        gap: var(--newsletter-form-label-gap);
        width: 100%;
        position: relative;
    }

    .zolo-form-label {
        font-size: 14px;
        color: var(--newsletter-form-label-color);
        text-transform: capitalize;
        font-weight: 500;
    }

    .zolo-form-input {
        padding: var(--newsletter-input-padding);
        border-width: var(--newsletter-input-border-width);
        border-color: var(--newsletter-input-border-color);
        border-style: var(--newsletter-input-border-style);
        border-radius: var(--newsletter-input-radius);
        outline: none;
        font-size: 15px;
        color: var(--newsletter-input-color);
        box-sizing: border-box;
        line-height: 1;
        min-height: unset;
        height: auto;
        background-color: transparent;
    }

    .zolo-form-input:focus {
        border-color: var(--newsletter-input-focus-color);
        box-shadow: none;
    }

    .zolo-form-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: var(--newsletter-btn-icon-gap);
        padding: var(--newsletter-btn-padding);
        border: 0;
        border-radius: var(--newsletter-btn-radius);
        font-size: 14px;
        background-color: #3f8efc;
        cursor: pointer;
        color: var(--newsletter-btn-color);
        transition: all 0.3s ease;
        .zolo-form-btn-text {
            text-wrap: nowrap !important;
        }
    }

    .zolo__display-icon svg {
        width: 18px;
        height: 18px;
        display: block;
        fill: #fff;
    }

    .zolo-form-btn:hover {
        background-color: #2667ff;
    }

    .zolo-form-control {
        width: 100%;
        box-sizing: border-box;
    }

    .zolo-form-submit-btn {
        width: auto;
    }

    .button-auto {
        .zolo-form-btn {
            width: fit-content;
        }
    }

    /* newsletter form style 2,3 */

    .zolo-newsletter-2 .zolo-form-label,
    .zolo-newsletter-3 .zolo-form-label,
    .zolo-newsletter-4 .zolo-form-label {
        position: absolute;
        transform: translateY(-50%);
        border-radius: 6px;
        left: 9px;
        padding: 0 6px;
    }
    .zolo-newsletter-2,
    .zolo-newsletter-3,
    .zolo-newsletter-4 {
        .zolo-form-input::placeholder {
            color: transparent !important;
        }
    }

    /* newsletter form style 2 */

    .zolo-newsletter-2 .zolo-form-label {
        top: 0;
        background-color: #fff;
    }

    /* newsletter form style 3 */

    .zolo-newsletter-3 .zolo-form-label,
    .zolo-newsletter-4 .zolo-form-label {
        top: 50%;
        cursor: text;
        pointer-events: none;
        font-weight: 400;
        transition: all 0.2s ease-in-out;
    }

    .zolo-newsletter-3 .zolo-form-input:focus ~ .zolo-form-label,
    .zolo-newsletter-3 .zolo-form-input:not(:placeholder-shown).zolo-form-input:not(:focus) ~ .zolo-form-label,
    .zolo-newsletter-4 .zolo-form-input:focus ~ .zolo-form-label,
    .zolo-newsletter-4 .zolo-form-input:not(:placeholder-shown).zolo-form-input:not(:focus) ~ .zolo-form-label {
        font-weight: 500;
        top: 0;
        background-color: #fff;
    }

    /* newsletter form style 4 */

    .zolo-newsletter-4 {
        --newsletter-form-item-gap: 10px;
        flex-direction: column;
        gap: var(--newsletter-form-item-gap);
    }

    .zolo-newsletter-4 .zolo-form-input {
        --newsletter-input-padding: 15px 15px;
        padding: var(--newsletter-input-padding);
    }

    .zolo-newsletter-button-style-2 .zolo-form-submit-btn {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }

    .zolo-newsletter-button-style-2 .zolo-form-btn {
        --newsletter-btn-padding: 10px 12px;
        padding: var(--newsletter-btn-padding);
        margin: 6px;
    }

    .zolo-form-notice {
        margin: 20px 0 0;
    }

    .zolo-form-notice-text {
        margin: 0;
        font-size: 15px;
        color: #68676d;
    }
    .zolo-newsletter-message {
        display: flex;
        flex-direction: column;
        margin-top: 18px;
        gap: 10px;
    }

    .zolo-newsletter-info-text {
        padding: 20px;
        border-radius: 6px;
        font-size: 16px;
        text-align: left;
        font-weight: 500;
        border-width: 1px;
        border-style: solid;
    }

    .status-success {
        color: #155724;
        background-color: #d4edda;
        border-color: #c3e6cb;
    }

    .status-warning {
        color: #856404;
        background-color: #fff3cd;
        border-color: #ffeeba;
    }

    .status-error {
        color: #721c24;
        background-color: #f8d7da;
        border-color: #f5c6cb;
    }
}

@media (min-width: 768px) {
    .wp-block-zolo-newsletter {
        .zolo-newsletter-4 {
            flex-direction: row;
        }
    }
}
